@font-face {
  font-family: "Matter";
  src: local("Matter"), url("../assets/fonts/Matter/Matter-Bold.woff2") format("woff2");
  src: local("Matter"), url("../assets/fonts/Matter/Matter-Bold.woff") format("woff");
  font-display: block; }

@font-face {
  font-family: "Matter-Heavy";
  src: local("Matter-Heavy"), url("../assets/fonts/Matter/Matter-Heavy.woff2") format("woff2");
  src: local("Matter-Heavy"), url("../assets/fonts/Matter/Matter-Heavy.woff") format("woff");
  font-display: block; }

@font-face {
  font-family: "DM Sans";
  src: local("DM Sans"), url("../assets/fonts/DMSans/DMSans-Regular.woff2") format("woff2");
  src: local("DM Sans"), url("../assets/fonts/DMSans/DMSans-Regular.woff") format("woff");
  font-display: block; }

body {
  font-family: 'DM Sans', 'sans-serif', 'serif';
  margin: 0;
  padding: 0;
  background-color: $bg-color;
  width: 100%;
  line-height: 1.3;
  box-sizing: border-box;
  overflow-x: hidden;
  position: relative;
  -webkit-tap-highlight-color: transparent;
  line-height: normal;
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0,0,0,0.5);
    z-index: 300;
    display: none;
    .close-btn {
      position: absolute;
      top: 36.5%;
      right: 50%;
      background-color: #fff;
      border-radius: 50%;
      padding: 5px;
      z-index: 350;
      transform: translateX(50%);
      a {
        svg {
          height: 20px;
          width: 20px;
          z-index: 200;
          stroke: $primary; } } } } }

* {
  box-sizing: border-box; }

h2,h3 {
  font-family: 'Matter-Heavy';
  font-weight: normal; }

button {
  font-family: 'DM Sans'; }

ul {
  list-style: none;
  text-decoration: none;
  margin: 0;
  padding: 0; }

a {
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-size: 1.0rem;
  font-weight: 600;
  outline: none;
  &:focus {
    outline: none; }
  &:active {
    color: $primary; } }

section {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center; }

div.content-wrapper {
  overflow-x: hidden; }

@media screen and (max-width: 780px) {
  body {
    .overlay {
      .close-btn {
        top: 37%; } } } }

@media screen and (max-width: 640px) {
  body {
    .overlay {
      .close-btn {
        top: 38%; } } } }

@media screen and (max-width: 420px) {
  body {
    .overlay {
      .close-btn {
        top: 37%; } } } }

@media screen and (max-width: 340px) {
  body {
    .overlay {
      .close-btn {
        top: 35%; } } } }


@media screen and (min-width: 1280px) {
  body {
    .overlay {
      .close-btn {
        top: 31%;
        right: 12%;
        transform: unset; } } } }
